import com.varabyte.kobweb.core.AppGlobals
import com.varabyte.kobweb.navigation.BasePath
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.navigation.remove
import com.varabyte.kobweb.silk.defer.DeferringHost
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable
import cbconnectit.portfolio.web.pages.HomePage as _cbconnectit_portfolio_web_pages_HomePage
import cbconnectit.portfolio.web.pages.projects.ProjectsPage as _cbconnectit_portfolio_web_pages_projects_ProjectsPage
import cbconnectit.portfolio.web.pages.services.ServicesPage as _cbconnectit_portfolio_web_pages_services_ServicesPage
import cbconnectit.portfolio.web.pages.services.service.ServicePage as _cbconnectit_portfolio_web_pages_services_service_ServicePage

public fun main() {
    AppGlobals.initialize(mapOf("title" to "Portfolio Web Compose", "BASE_URL" to "https://cb-connect-it.com"))
    BasePath.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register(
            "/",
        ) { pageCtx -> 
            _cbconnectit_portfolio_web_pages_HomePage()
        }
        ctx.router.register(
            "/projects",
        ) { pageCtx -> 
            _cbconnectit_portfolio_web_pages_projects_ProjectsPage()
        }
        ctx.router.register(
            "/services",
        ) { pageCtx -> 
            _cbconnectit_portfolio_web_pages_services_ServicesPage()
        }
        ctx.router.register(
            "/services/service",
        ) { pageCtx -> 
            _cbconnectit_portfolio_web_pages_services_service_ServicePage()
        }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerStyle("services-type-card", cbconnectit.portfolio.web.components.ServicesTypeCardStyle)
        ctx.theme.registerStyle("project-description", cbconnectit.portfolio.web.pages.sections.ProjectDescriptionStyle)
        ctx.theme.registerStyle("project-name", cbconnectit.portfolio.web.pages.sections.ProjectNameStyle)
        ctx.theme.registerStyle("service-section", cbconnectit.portfolio.web.pages.sections.serviceSectionStyle)
        ctx.theme.registerStyle("valid-feedback", com.materialdesignsystem.styles.validFeedbackStyle)
        ctx.theme.registerStyle("invalid-feedback", com.materialdesignsystem.styles.invalidFeedbackStyle)
        ctx.theme.registerStyle("button-size-xl", com.materialdesignsystem.extensions.ButtonSizeXL)
        ctx.theme.registerStyle("ds-external-link", com.materialdesignsystem.components.widgets.DsExternalLinkStyle)
        ctx.theme.registerStyle("checked-input", com.materialdesignsystem.components.widgets.CheckedInputStyle)
        ctx.theme.registerStyle("select", com.materialdesignsystem.components.widgets.SelectStyle)
        ctx.theme.registerStyle("valid-select", com.materialdesignsystem.components.widgets.validSelectStyle)
        ctx.theme.registerStyle("invalid-select", com.materialdesignsystem.components.widgets.invalidSelectStyle)
        ctx.theme.registerStyle("select-form-floating", com.materialdesignsystem.components.widgets.SelectFormFloating)
        ctx.theme.registerStyle("select-defaults_size", com.materialdesignsystem.components.widgets.SelectDefaults.Size)
        ctx.theme.registerStyle("select-size_xs", com.materialdesignsystem.components.widgets.SelectSize.Companion.XS)
        ctx.theme.registerStyle("select-size_sm", com.materialdesignsystem.components.widgets.SelectSize.Companion.SM)
        ctx.theme.registerStyle("select-size_md", com.materialdesignsystem.components.widgets.SelectSize.Companion.MD)
        ctx.theme.registerStyle("select-size_lg", com.materialdesignsystem.components.widgets.SelectSize.Companion.LG)
        ctx.theme.registerStyle("silk-text-area", com.materialdesignsystem.components.widgets.TextAreaStyle)
        ctx.theme.registerStyle("multi-select", com.materialdesignsystem.components.widgets.MultiSelectStyle)
        ctx.theme.registerStyle("multi-select-defaults_size", com.materialdesignsystem.components.widgets.MultiSelectDefaults.Size)
        ctx.theme.registerStyle("multi-select-size_xs", com.materialdesignsystem.components.widgets.MultiSelectSize.Companion.XS)
        ctx.theme.registerStyle("multi-select-size_sm", com.materialdesignsystem.components.widgets.MultiSelectSize.Companion.SM)
        ctx.theme.registerStyle("multi-select-size_md", com.materialdesignsystem.components.widgets.MultiSelectSize.Companion.MD)
        ctx.theme.registerStyle("multi-select-size_lg", com.materialdesignsystem.components.widgets.MultiSelectSize.Companion.LG)
        ctx.theme.registerStyle("ds-card", com.materialdesignsystem.components.widgets.DsCardStyle)
        ctx.theme.registerStyle("navigation-item", com.materialdesignsystem.components.sections.NavigationItemStyle)
        ctx.theme.registerVariant("-elevated-icon-button-style", com.materialdesignsystem.components.widgets.ElevatedIconButtonStyle
        )
        ctx.theme.registerVariant("-filled-icon-button-style", com.materialdesignsystem.components.widgets.FilledIconButtonStyle
        )
        ctx.theme.registerVariant("-outlined-icon-button-style", com.materialdesignsystem.components.widgets.OutlinedIconButtonStyle
        )
        ctx.theme.registerVariant("-filled-tonal-icon-button-style", com.materialdesignsystem.components.widgets.FilledTonalIconButtonStyle
        )
        ctx.theme.registerVariant("-icon-button-style", com.materialdesignsystem.components.widgets.IconButtonStyle
        )
        ctx.theme.registerVariant("-outlined-select-style", com.materialdesignsystem.components.widgets.OutlinedSelectStyle
        )
        ctx.theme.registerVariant("-unstyled-select-style", com.materialdesignsystem.components.widgets.UnstyledSelectStyle
        )
        ctx.theme.registerVariant("-", com.materialdesignsystem.components.widgets.SelectDefaults.Variant
        )
        ctx.theme.registerVariant("-outlined", com.materialdesignsystem.components.widgets.OutlinedTextAreaVariant
        )
        ctx.theme.registerVariant("-outlined-multi-select-style", com.materialdesignsystem.components.widgets.OutlinedMultiSelectStyle
        )
        ctx.theme.registerVariant("-", com.materialdesignsystem.components.widgets.MultiSelectDefaults.Variant
        )
        ctx.theme.registerVariant("-elevated-button-style", com.materialdesignsystem.components.widgets.ElevatedButtonStyle
        )
        ctx.theme.registerVariant("-filled-button-style", com.materialdesignsystem.components.widgets.FilledButtonStyle
        )
        ctx.theme.registerVariant("-outlined-button-style", com.materialdesignsystem.components.widgets.OutlinedButtonStyle
        )
        ctx.theme.registerVariant("-filled-tonal-button-style", com.materialdesignsystem.components.widgets.FilledTonalButtonStyle
        )
        ctx.theme.registerVariant("-text-button-style", com.materialdesignsystem.components.widgets.TextButtonStyle
        )
        ctx.theme.registerKeyframes("rotation-key-frames", com.materialdesignsystem.components.widgets.rotationKeyFrames)
        cbconnectit.portfolio.web.updateTheme(ctx)
        com.materialdesignsystem.updateTheme(ctx)
    }

    // Dedup any leading slashes after removing the origin, just in case someone typed
    // something like `https://site.com//about` by accident. If we pass `//about` into
    // `tryRoutingTo`, Kobweb will reject it as a protocol-relative URL; instead, we
    // want it to navigate to `/about`
    router.tryRoutingTo(BasePath.remove("/" + window.location.href.removePrefix(window.origin).trimStart('/')), UpdateHistoryMode.REPLACE)

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("_kobweb-root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    renderComposable(rootElementId = "_kobweb-root") {
        cbconnectit.portfolio.web.AppEntry {
            router.renderActivePage { DeferringHost { it() } }
        }
    }
}
